// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Panel,
} from 'react-bootstrap';

// custom
import {
  treasureImgs,
  itemImgs,
  sortText,
} from '../lib/data';
import {
  Skill,
  Value,
  SimpleNameko,
} from './Nameko';

const ResultGroup = ({ text }) => <div
  className='result-group'
>
  <img className='success' src='./imgs/adventure_success.png' alt='success' />
  {text}
</div>;

const Reward = ({ type, name }) => <span
  className='reward'
  style={{ backgroundImage: `url(./imgs/adventure_${type}.png)` }}
>
  <img src={(type === 'treasure' ? treasureImgs : itemImgs)[name]} alt={name}/>
  <span className={type}>{name}</span>
</span>;

class Result extends Component {
  render() {
    const { result/*, tunnel*/ } = this.props;
    const { team, /*passedMemos, */unpassedMemos } = result;

    return (
      <Panel className='result'>
        <div className='namekos'>
          {team.map((n, idx) => <SimpleNameko key={idx} name={n.name} />)}
        </div>
        {unpassedMemos.map((memo, idx) => {
          const { type, reward: r, valueThresholds: v, skills: s, name } = memo;

          return (
            <div key={idx}>
              <img className='fail' src='./imgs/adventure_fail.png' alt='fail' />
              {'Fail: '}
              <Reward type={memo.rewardType} name={r[0]} />
              {`*${r[1]} ${name}, ${type}`}
              {v && v.map((v, idx) => <Value key={idx} type={v[0]} value={v[1]} />)}
              {s && s.map((s, idx) => <Skill key={idx} skill={s} />)}
            </div>
          );
        })}
      </Panel>
    );
  }
}

export default class extends Component {
  render() {
    if (!this.props.results) {
      return null;
    }

    const { tunnel } = this.props;
    const { results, sortCond } = this.props.results;
    const { group, name, memos } = this.props.tunnel;

    const comps = [];

    results.slice(0, 1000).forEach((r, idx) => {
      const { passedMemos, count } = r;

      const r2 = results[idx - 1];

      if (sortCond === 'byPassCount') {
        if (!r2 || passedMemos.length !== r2.passedMemos.length) {
          comps.push({
            header: <ResultGroup key={idx} text={`全部${memos.length}, 通過${passedMemos.length}`} />,
            items: [],
          });
        }
      } else if (sortCond === 'byTreasure') {
        if (!r2 || passedMemos.length !== r2.passedMemos.length || count.treasureMemo !== r2.count.treasureMemo) {
          comps.push({
            header: <ResultGroup key={idx} text={`全部${memos.length}, 通過${passedMemos.length}(寶藏${count.treasureMemo})`} />,
            items: [],
          });
        }
      } else if (sortCond === 'byItem') {
        if (!r2 || passedMemos.length !== r2.passedMemos.length || count.itemMemo !== r2.count.itemMemo) {
          comps.push({
            header: <ResultGroup key={idx} text={`全部${memos.length}, 通過${passedMemos.length}(道具${count.itemMemo})`} />,
            items: [],
          });
        }
      }

      comps[comps.length - 1].items.push(<Result key={idx} result={r} tunnel={tunnel} />);
    });

    return (
      <Panel className='result-panel'>
        {!!results.length && <h1>{`${group}(${name}), ${sortText[sortCond]}`}</h1>}
        {comps.map((comp, idx) => (
          <div key={idx}>
            {comp.header}
            {comp.items}
          </div>
        ))}
      </Panel>
    );
  }
}
