/* Tunnel */

export const allTunnels = tunnels;
tunnels.forEach(t => t.memos.forEach(m => {
  const reward = m.reward[0];
  if (allTreasure.includes(reward)) {
    m.rewardType = 'treasure';
  } else {
    m.rewardType = 'item';
  }
}));

