// react
import React, { Component } from 'react';

// react-bootstrap
import {
  Button,
  Col,
  Glyphicon,
  Image,
} from 'react-bootstrap';

// custom
import {
  skillImgs,
  namekoSkills,
  namekoImgs,
} from '../lib/data';

export const Skill = ({ skill, isSelected = true }) => (<span
  style={{ backgroundImage: `url(${skillImgs[skill]})` }}
  className={'skill' + (isSelected ? '' : ' unselected')}
></span>);

export const Value = ({ type, value }) => (<span
  className='value'
  style={{ backgroundImage: `url(./imgs/${type}.png` }}
>
  {value}
</span>);

export class SimpleNameko extends Component {
  render() {
    const { name, hideText } = this.props;

    return (
      <div className='simple-nameko'>
        <Image
          className='img'
          src={namekoImgs[name]}
        />
        {!hideText && <div className='name'>{name}</div>}
      </div>
    );
  }
}

export default class extends Component {
  render() {
    const { nameko, isSelected, onClick, onClickEdit } = this.props;
    const { name, skills, values } = nameko;

    const className = 'nameko' + (isSelected ? ' selected' : '') + (onClick ? ' clickable' : '');

    return (
      <Col className='nameko-wrapper' xs={4} sm={3} md={2}>
        <div className={className}>
          <div className='name'>{name}</div>
          <div
            className='top-ctn'
            onClick={() => onClick && onClick(nameko)}
          >
            <SimpleNameko name={name} hideText={true} />
            <div className='value-ctn'>
              {values.map((value, idx) => <Value
                key={idx}
                type={['red', 'blue', 'green'][idx]}
                value={value}
              />)}
            </div>
          </div>
          <div className='skill-ctn'>
            {namekoSkills[name].map((skill, idx) => <Skill
              key={idx}
              skill={skill}
              isSelected={skills.indexOf(skill) !== -1}
            />)}
          </div>
          {onClickEdit && <div>
            <Button onClick={() => onClickEdit(nameko)}>
              <Glyphicon glyph='pencil' />
              編輯
            </Button>
          </div>}
        </div>
      </Col>
    );
  }
}
